.grid-img {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(185px, 1fr));
	grid-gap: 20px;
}

.container {
	width: 1200px;
	max-width: 100%;
	margin: auto;
	padding: 20px;
	margin-top: 50px;

	@media screen and (max-width: 1068px) {
		margin-top: 0px;
	}
}

.img-crea {
	width: 100%;
	height: 385px;
	display: block;
	border: 10px solid orangered;
	transform: scale(1);
	transition: 0.3s ease-in-out;

	&:hover {
		transform: scale(1.1);
		transition: 1s ease;
	}
}

.left-infs {
	width: 50%;
	max-width: 100%;
	position: sticky;
	top: 0px;
	height: 100%;
	z-index: 9999;
	padding: 0 20px;
}

.right-infs {
	width: 48%;
	max-width: 100%;
	margin-left: 3%;
}

.id {
	font-size: 45px;
	margin-top: 30px;
	margin-bottom: 30px;
	transition: font-size 0.3s ease-in-out;
}

.name-2 {
	font-size: 55px;
	text-transform: uppercase;
	font-family: "roboto";
	font-weight: bold;
}

.item {
	font-size: 55px;
	text-transform: capitalize;
	font-weight: bold;
	font-family: "roboto";
	margin-bottom: 30px;
}

.descriptions {
	width: 450px;
	max-width: 90%;
	line-height: 1.6;
}

.ml-15 {
	margin-left: 15px;
}

.btns {
	position: fixed;
	width: 1200px;
	max-width: 100%;
	background: #000;

	button {
		cursor: pointer;
		position: absolute;
		outline: none;
		top: 50%;
		transform: translateY(-50%);
		width: 40px;
		height: 40px;
		text-align: center;
		display: block;
		font-size: 1.2em;
		color: #f44336;
		font-weight: bold;
		border: 1px solid #f44336;
		background: transparent;
		border-radius: 50%;
		text-align: center;
		transition: 0.3s ease-in-out;
		transform: scale(1.1);

		&:nth-child(1) {
			left: -75px;
		}

		&:nth-child(2) {
			right: -50px;

			margin-top: -4px;
		}

		&:hover {
			transform: scale(0.9);
		}
	}
}

.content {
	margin-top: 0px;
	.content-txt {
		transition: all 0.5s ease-in-out;
		animation: zoom 0.5s ease-in-out;
		transform: scale(1);
	}
}

@keyframes zoom {
	0%,
	100% {
		transform: scale(0);
	}
	100% {
		transform: scale(1);
	}
}

.arrow-left {
	@media screen and (max-width: 1068px) {
		margin-top: -30px !important;
		font-size: 30px !important;
		font-weight: bold;
	}
}
.f-size-35px {
	font-size: 35px;
}

.f-size-45px {
	text-transform: uppercase;
	@media screen and (max-width: 1068px) {
		font-size: 30px !important;
		margin-top: 20px !important;
	}
}

.size-txt {
	width: 60%;
	max-width: 100%;
	margin: auto;
	display: block;
	line-height: 1.7;

	@media screen and (max-width: 1068px) {
		font-size: 20px !important;
	}
}

.slider {
	position: absolute;
}

.padding-portfolio {
	padding: 60px;
}

.img {
	max-width: 100%;
}

.width-60 {
	max-width: 60%;
	width: 100%;
	margin: auto;
	margin-top: 140px;
	display: block;

	@media screen and (max-width: 767px) {
		max-width: 100%;
		margin-top: 70px;
	}
}

.img-portfolio {
	width: 100%;
	max-width: 100%;
	height: 100%;
	margin: auto;
	display: block;
}

.infos {
	width: 50%;
	max-width: 100%;

	h2 {
		font-size: 35px;
		font-weight: bold;
		margin-bottom: 15px;
	}

	p {
		font-size: 30px;
	}
}

.mt {
	margin-top: 40%;
}

.txt-uppercase {
	text-transform: uppercase;
}
.ml {
	margin-left: 50%;
}

.ml-0 {
	margin-left: 0;
}

.row-reverse {
	flex-direction: row-reverse !important;
}

.mr {
	margin-right: 50%;
}

// PRINT CREATIONS

// Affiche cinema
.content-portfolio {
	padding-bottom: 100px;
}

.dark-blue-color {
	color: #12283f;
}

.content-txt-portfolio {
	width: 73%;
	max-width: 100%;
	margin: auto;
	display: block;
	margin-top: 80px;
	text-align: center;
	color: #12283f !important;

	@media screen and (max-width: 1068px) {
		width: 100%;
		padding: 0 25px;
		text-align: left !important;
		margin-top: 50px;
	}
}

.title-portfolio {
	width: 70%;
	max-width: 100%;
	font-size: 40px;
	margin: auto;
	margin-top: 15px;
	display: block;
	text-align: left;

	@media screen and (max-width: 1068px) {
		width: 100%;
		font-size: 30px;
	}
}

.txt-portfolio {
	width: 70%;
	margin: auto;
	display: block;
	text-align: left;
	max-width: 100%;
	line-height: 1.7;
	margin-top: 25px;
	font-size: 25px;

	@media screen and (max-width: 1068px) {
		width: 100%;
	}
}

.container-horizontal-slide {
	width: 100%;
	padding: 1px;
	min-height: 500px;
	background: #12283f;
	margin-top: 50px;
}

.controls {
	padding: 110px 0;
	background-color: #021d35;
	.controls-name {
		text-decoration: none;
		color: #fff;
		transition: all 0.5s ease-in-out;

		&:hover {
			color: #ffd600;
		}
	}
}

.icon-prev {
	margin-right: 20px;
}

.icon-controls {
	font-size: 20px;
}

.icon-back {
	color: #fff;
	font-size: 30px;
	transform: scale(1);
	transition: all 0.5s ease;

	&:hover {
		transform: scale(1.4);
		color: #ffd600;
	}
}

.icon-next {
	margin-left: 20px;
}

.space-around {
	display: flex;
	justify-content: space-around;
	align-items: center;
	justify-items: center;
}

@media screen and(max-width: 768px) {
	.controls {
		padding: 90px 0;
	}
	.controls-name span {
		display: none;
	}
}

.main-portfolio {
	transition: all 0.5s ease-in-out;
	animation: fade 0.5s ease;
	opacity: 1;
}

@keyframes fade {
	0%,
	100% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

video.videos {
	margin-top: 60px;
}

.insta-videos {
	max-width: 350px;
	margin: auto;
	display: block;
	width: 100%;
	margin-top: 60px;

	@media screen and (min-width: 280px) and (max-width: 450px) {
		max-width: 250px;
	}
}
