#a-propos,
#mes-atouts,
#identite-de-marque,
#webdesign,
#illustrations-service {
  // margin-top: 200px;
  // padding-top: 30px;
  // padding-bottom: 30px;
  @media screen and (max-width: 1068px) {
    // padding-top: 20px !important;
    // padding-bottom: 50px !important;
  }

  p {
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .content-about {
    display: flex;

    @media screen and (max-width: 768px) {
      display: block;
    }

    .bloc-txt-about {
      width: 54%;
      max-width: 90%;
      padding-right: 70px;

      @media screen and (max-width: 1668px) {
        padding-right: 40px;
        padding-left: 45px;
      }

      @media screen and (max-width: 1068px) {
        width: 100%;
        padding-right: 0;
        padding-left: 25px !important;
      }

      .title-about {
        font-size: 55px;
        font-weight: bold;
        @media screen and (max-width: 1068px) {
          font-size: 35px !important;
        }
      }

      .txt-about {
        margin-top: 25px;
        font-size: 35px;
        line-height: 1.7;
        @media screen and (max-width: 1068px) {
          font-size: 22px !important;
        }
      }
    }

    .img-about {
      width: 680px;
      max-width: 100% !important;
      @media screen and (max-width: 1068px) {
        width: 100%;
        display: block;
      }
    }

    @media screen and (max-width: 1068px) {
      display: block;
    }
  }
}

.bloc-about.bloc-txt-about {
  margin-top: -40px;
  @media screen and (max-width: 1068px) {
    margin-top: 0;
  }
}

.container-main {
  width: 1400px;
  max-width: 100%;
  margin: auto;
}

.row-reverse {
  flex-direction: row-reverse;
}

.padding-left {
  padding-left: 40px;

  @media screen and (max-width: 768px) {
    padding-left: 25px !important;
  }
}

// .padding-services {
//   padding: 60px 0;
// }

.padding-services {
  padding-top: 60px;
  &:nth-child(odd) {
    padding-bottom: 60px;
  }
  &:nth-child(even) {
    padding-bottom: 0px;
  }
  @media screen and (max-width: 1068px) {
    padding-top: 30px !important;
    padding-bottom: 0px !important;
  }
}
