.icon-footer {
	width: 100%;
	display: block;
	margin-bottom: 20px;
	z-index: 98;
}

.content-icon {
	width: 35px;
}

.content-icon-big {
	width: 100px;
	margin: auto;
	display: block;
	margin-top: 50px;
}

.icon-instagram:hover .icon-footer,
.icon-instagram-big:hover .icon-footer,
.icon-linkedin:hover .icon-footer,
.icon-youtube:hover .icon-footer,
.icon-behance:hover .icon-footer {
	opacity: 0;
}

.icon-instagram:hover {
	background-image: url("../../images/home/footer/instagram.svg");
	width: 35px;
	background-repeat: repeat;
	background-size: cover;
}

.icon-instagram-big:hover {
	background-image: url("../../images/home/footer/instagram.svg");
	width: 100px;
	background-repeat: repeat;
	background-size: cover;
}

.icon-linkedin:hover {
	background-image: url("../../images/home/footer/linkdin.svg");
	width: 35px;
	background-repeat: repeat;
	background-size: cover;
}

.icon-youtube:hover {
	background-image: url("../../images/home/footer/youtube.svg");
	width: 37px;
	background-repeat: repeat;
	background-size: cover;
}

.icon-behance:hover {
	background-image: url("../../images/home/footer/behance.svg");
	width: 37px;
	background-repeat: repeat;
	background-size: cover;
}

#footer {
	position: relative;
	padding-bottom: 30px;
	background-color: #0e2840;
	// background: #0E2840 url("../../images/home/footer/logo\ nav\ barre.png") no-repeat;
	// background-size: 120px;
	// background-position: 80% 15%;

	@media screen and(max-width: 1068px) {
		padding-top: 50px;
		// background-size: 110px;
		// background-position: 10% 29%;
	}

	.content-footer {
		width: 800px;
		max-width: 90%;
		margin: auto;
		display: block;

		.title-footer {
			width: 700px;
			max-width: 100%;
			font-size: 40px;
			color: #fff;

			@media screen and (max-width: 1068px) {
				font-size: 25px;
			}
		}

		.grid-footer-infos {
			display: grid;
			// grid-template-columns: repeat(3, 1fr);
			grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
			grid-gap: 20px;
			margin-top: 80px;

			.infos-footer {
				padding: 10px;
				// min-height: 300px;
				color: #ffd600;
				line-height: 1.9;
				margin-top: -30px;

				.link-footer {
					text-decoration: none;
					color: #ffd600;
					display: block;
					line-height: 2;
					transition: 0.3s ease-in-out;

					&:hover {
						color: #fff;
					}
				}

				.name-footer-infos {
					font-size: 25px;
					color: #fff;
					margin-bottom: 15px;
				}
			}
		}
		a {
			color: #fff;
			transition: all 0.5s ease;

			&:hover {
				transition: all 0.5s ease;
			}
		}
	}

	.underline {
		text-decoration: underline !important;
	}

	.mentions-legales {
		text-align: center;
		color: #fff;
		font-size: 0.9em;
		margin-top: 55px;
	}
}

.padding-top-100 {
	padding-top: 100px;
}

.padding-top-20 {
	padding-top: 20px;
}

.d-flex {
	display: flex;
	@media screen and (max-width: 1068px) {
		display: block;
	}
}
.logo-footer {
	width: 120px;
	height: 144px;
	margin-top: 35px;
	@media screen and (max-width: 1068px) {
		width: 100px;
		height: 124px;
		margin-top: 20px;
		margin-left: 5px;
	}
}

.hover-rules {
	&:hover {
		color: #ffd600 !important;
	}
}
