.nav {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
  width: 100%;
  max-width: 100%;
  padding: 0 25px;
  background-color: #ffe465;
  opacity: 1;
  transition: opacity 0.5s ease;

  .logo {
    // margin-top: 15px;
    width: 75px;

    .logo-size {
      width: 75px;
      margin-top: 5px;
    }
  }

  .list-menu {
    .list-items {
      list-style-type: none;

      .anchors-items {
        cursor: pointer;
        // padding: 25px;
        // display: block;
        line-height: 100px;
        padding: 0 15px;
        text-decoration: none;

        // transition: 0.3s ease-in-out;

        @media screen and (max-width: 1068px) {
          line-height: 80px;
        }

        &:hover {
          color: #ffd600;
          text-shadow: 0.5px 0.5px 0.5px rgba(2, 29, 53, 0.5);
        }
      }
    }

    .sub-menu {
      position: absolute;
      opacity: 0;
      visibility: hidden;
      display: none;
      background: #4badee;
      margin-left: -50px;
      z-index: 99;
      transition: 0.3s ease-in-out;

      @media screen and (max-width: 1068px) {
        width: 100%;
        left: 0 !important;
        right: 0 !important;
        margin: 0;
      }

      .list-items {
        cursor: pointer;
        background: #4badee;
        box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
        // margin-bottom: 5px;
        border-bottom: 1px solid #73bef0;
        text-align: center;
        // transition: .4s ease-in;

        &:hover {
          background: #e61b76;
        }

        .anchors-items {
          padding: 15px;
          line-height: 50px;
          color: #fff;
        }

        &:last-of-type {
          border-bottom: none;
        }
      }
    }
  }
}

.flex {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media screen and (max-width: 1068px) {
    display: block;
    text-align: center;
  }
}

.flex-list {
  display: flex;
  @media screen and (max-width: 1068px) {
    display: none;
    text-align: center;
  }
}

.show {
  display: block;
}

.menu-icon {
  position: absolute;
  right: 25px;
  top: 35px;
  opacity: 0;
  cursor: pointer;

  .icon {
    width: 30px;
    height: 2.5px;
    background: #e61b76;
    margin-bottom: 6px;
  }

  @media screen and (max-width: 1068px) {
    opacity: 1;
  }
}

.showCaret {
  opacity: 1 !important;
  visibility: visible !important;
  display: block !important;
  margin-bottom: 1000px;
}

.anchors-items {
  color: #4c0bd9;
}

.color {
  color: red !important;
}

.nav-affix {
  position: sticky !important;
  transition: opacity 0.5s ease;
  animation: navAffix 0.5s ease-in-out;
  opacity: 1;
}

@keyframes navAffix {
  0%,
  100% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

