.container-design {
	width: 100% !important;
	max-width: 100%;
	margin: auto;
	display: block;
	margin-top: 50px;
}

.box-creations {
	position: relative;
	width: 325px;
	transition: 0.3s ease-in-out;

	@media screen and (max-width: 1068px) {
		min-height: 300px !important;
	}

	.block-img-default {
		img {
			width: 100%;
			height: 100%;
			display: block;
		}
	}
}

.grid-creations,
.filter-item {
	display: grid;
	grid-template-columns: repeat(1, 1fr);
}

.filter-item {
	width: calc(100% / 3);
	height: 25vw !important; // margin-top: 40px;
	@media screen and (max-width: 1068px) {
		width: 100%;
		height: auto !important;
	}
}

//

.hide {
	opacity: 0;
}

.layer-infos {
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;
	background: rgba(75, 173, 238, 0.89);
	// background: #000 url("../../hover-galerie-cadre.png") no-repeat center;
	background-size: cover;
	transition: 0.4s ease-in-out;
	// padding: 20px;
}

.absolute-center-creations {
	width: 100%;
	position: absolute;
	padding: 20px;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	text-transform: uppercase;
	text-align: center;
}

.items-project-name {
	margin-bottom: 15px;
	color: #fff;
	font-weight: normal;
}

.title-project-name {
	font-size: 25px;
	color: #fff;
}

.box-creations:hover .hide,
.box-creations:hover .layer-infos {
	opacity: 1;
	transition: 0.3s ease-in;
}
