#profil {
  padding: 120px 0;
  padding-left: 35px;
  background: #4badee;
  @media screen and (max-width: 1068px) {
    padding: 60px 0;
    padding-bottom: 0;
  }

  .content-about {
    display: grid;
    grid-template-columns: 650px 640px;
    // grid-gap: 100px;

    @media screen and (max-width: 1068px) {
      display: block;
      max-width: 100%;
    }

    .bloc-txt-about {
      color: #fff;
      @media screen and (max-width: 1068px) {
        padding: 20px;
      }

      .title-profil {
        font-size: 55px;
        font-weight: bold;

        @media screen and (max-width: 1068px) {
          font-size: 35px;
        }
      }

      .txt-profil {
        margin-top: 25px;
        margin-bottom: 25px;
        font-size: 25px;
        line-height: 1.8;

        @media screen and (max-width: 1068px) {
          font-size: 22px;
        }
      }
    }
  }

  .img-profil {
    width: 580px;
    max-width: 100%;
  }
}

.container-main-2 {
  width: 1550px;
  max-width: 100%;
  margin: auto;
}

#other-passion {
  background: #4badee;
  padding: 120px 0px;

  @media screen and (max-width: 1068px) {
    padding: 40px 0;
    padding-bottom: 60px;
  }

  .content-other-passion {
    display: grid;
    grid-template-columns: 650px 600px;
    grid-gap: 50px;

    @media screen and (max-width: 1068px) {
      display: block;
    }
    .bloc-content {
      // color: #4c0bd9;
      padding: 0px;

      &:nth-child(1) {
        padding: 20px;
      }

      .txt-other-passion {
        font-size: 25px;
        color: #fff;

        @media screen and (max-width: 1068px) {
          font-size: 22px;
        }
      }

      .grid-img {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 20px;
        @media screen and (max-width: 1068px) {
          display: block;
        }

        .img {
          position: relative;
          transition: 0.3s ease-in;
          background: #fff;

          img {
            width: 100%;
            display: block;
          }

          .layer {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            background: rgba(255, 214, 0, 0.84);
            transition: 0.3s ease-in-out;
            visibility: hidden;
            opacity: 0;

            .content-layer {
              width: 100%;
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              color: #fff;
              text-align: center;

              .title-content {
                font-size: 1.8em;
                padding: 5px 35px;
              }
            }
          }
          &:hover .layer {
            visibility: visible;
            opacity: 1;
          }

          .img-other-passion {
            width: 170px;
            height: 330px;
            margin-left: 75px;
            display: block;
          }

          // &:hover {
          //     border: 16px solid #ffd600;
          // }
        }
      }
    }
  }
}

.img-cintic {
  max-width: 100% !important;
}
