.bg-header {
  position: relative;
  background-image: url("/img/home/header/fond-index-emilie.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  background-position: center center;

  @media screen and (max-width: 1068px) {
    position: static;
    max-width: 100% !important;
  }

  .content {
    width: 52%;
    max-width: 100%;
    position: absolute;
    top: 35%;
    transform: translateY(-35%);
    padding: 15px 65px;
    color: #fff;
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);

    .text-header-presentation {
      font-size: 1.2em;
      margin-top: 15px;
    }

    @media screen and (max-width: 1068px) {
      width: 100%;
      font-size: 1em;
      top: 50%;
      margin-top: -50px;
      transform: translateY(-40%);
      padding: 25px;
    }

    @media screen and (max-width: 768px) {
      margin-top: 50px;
    }
    @media screen and (max-width: 375px) {
      margin-top: 20px;
    }
  }

  .scrollDown-btn {
    width: 100%;
    position: absolute;
    bottom: 35px;
    text-align: center;
    color: #fff;

    .scrollDown {
      cursor: pointer;
      font-size: 2.5em;
      margin-bottom: 5px;
      transition: 0.3s ease-in-out;

      &:hover {
        transform: scale(0.8);
      }
    }
  }
}

.link-anchors {
  text-decoration: none;
  color: #fff;
}
