/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  /* INDEX PAGE */
  // Header
  .bg-header {
    background-image: url("/img/home/header/fond-index-emilie-1000.jpg") !important;
    background-size: 1000px 657px !important;
    min-height: 657px !important;
  }
  /* CONTACT PAGE */
  .contact-picture {
    content: url("./images/contact/emilie-500.png");
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  //CONTACT PAGE
  .contact-picture {
    content: url("./images/contact/emilie-500.png");
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  //CONTACT PAGE
  .contact-picture {
    content: url("./images/contact/emilie-768.png");
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  //CONTACT PAGE
  .contact-picture {
    content: url("./images/contact/emilie-1224.png");
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  //CONTACT PAGE
  .contact-picture {
    content: url("./images/contact/emilie-1224.png");
  }
}
